<template>
  <fullscreen  ref="fullscreen">
    <el-container>
      <!-- 226px -->
      <el-aside :width="collapse?'64px':'226px'">
        <!-- 左侧导航 -->
        <h3 class="logo-title">小U商城后台</h3>
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          background-color="#333"
          text-color="#fff"
          active-text-color="#fff"
          router
          :collapse="collapse"
          :collapse-transition="false"
          :unique-opened="true"
        >
          <el-menu-item index="/">
            <i class="el-icon-menu"></i>
            <span slot="title">管理中心</span>
          </el-menu-item>
          <!-- index数据类型必须值字符串 -->
          <el-submenu :index="'index'+item.id" v-for="item in menus" :key="item.id">
            <template slot="title">
              <i :class="item.icon"></i>
              <span>{{item.title}}</span>
            </template>
            <el-menu-item v-for="val in item.children" :key="val.id" :index="val.url">{{val.title}}</el-menu-item>
          </el-submenu>
        
        </el-menu>
      </el-aside>
      <el-container>
        <el-header>
          <!-- 折叠菜单按钮 -->
          <el-button @click="toggle" type="primary" size="mini" icon="el-icon-s-fold"></el-button>
          <!-- 面包屑导航 -->
          <el-breadcrumb separator="/">
            <el-breadcrumb-item to="/">管理中心</el-breadcrumb-item>
            <el-breadcrumb-item>{{$route.meta.title}}</el-breadcrumb-item>
          </el-breadcrumb>
          <!-- 返回按钮 -->
          <el-button @click="$router.go(-1)" circle size="mini" icon="el-icon-back"></el-button>

          <!-- 下拉菜单 -->
          <el-dropdown  @command="handleCommand">
            <span class="el-dropdown-link">
              admin
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="personInfo"><i class="el-icon-user"></i>个人信息</el-dropdown-item>
              <el-dropdown-item command="fullscreen"><i class="el-icon-full-screen"></i>全屏预览</el-dropdown-item>
              <el-dropdown-item command="logout"><i class="el-icon-switch-button"></i>安全退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>

        </el-header>
        <el-main>
            <!-- 二级路由出口 -->
            <router-view />
        </el-main>
      </el-container>
    </el-container>
  </fullscreen >
</template>

<script>
import Vue from 'vue'
// 导入全屏组件vue-fullscreen
import fullscreen from 'vue-fullscreen'
// 注册插件
Vue.use(fullscreen)
import {mapState,mapMutations} from 'vuex'
export default {
  data(){
    return {
      // true: 折叠, false展开
      collapse:false
    }
  },
  computed:{
    ...mapState(['menus'])
  },
  methods: {
    ...mapMutations(['menusInit']),
    // 控制左侧导航菜单的折叠状态
    toggle(){
      this.collapse=!this.collapse
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleCommand(command){
      // console.log(command)
      this[command]()
    },
    // 查看个人信息
    personInfo(){

    },
    //实现全屏预览
    fullscreen(){
      this.$refs.fullscreen.toggle()
    },
    // 安全退出
    logout(){
        // 1-清除本地存储中的登录信息
        localStorage.removeItem('logininfo')
        // 2-跳转到登录页面
        this.$router.push('/login')
    }
  },
  created(){
    this.menusInit()
  }
}
</script>

<style scoped>
.el-container {
  height: 100vh;
  overflow: hidden;
}

.logo-title {
  line-height: 60px;
  text-align: center;
  background: #444;
  color: #fff;
  font-weight: normal;
  letter-spacing: 4px;
  /* 强制文字不换行 */
  white-space: nowrap;
}
.el-menu {
  height: calc(100% - 60px);
  border-right: none;
  text-align: left;
}
/* 导航高亮样式 */
.is-active {
  background: #409eff !important;
}

/* 头部 */
.el-header {
  position: relative;  
  background-color: #fff;
  color: #333;
  /* line-height: 60px; */
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  align-items: center;
}
.el-breadcrumb {
  margin: 0 10px;
}
.el-dropdown{
    position: absolute;
    top:50%;
    transform: translateY(-50%);
    right:20px;
}
.el-main {
  background-color: #e9eef3;
}

</style>


